nav {
  background: url('../img/nav-red-bg.svg') no-repeat, white;
  background-position: left 0 top 0, auto; 
  background-size: 78% 4em, auto;
  box-shadow: 0 0 0.2em rgba(0,0,0,0.3);
  height: 4em;
  padding: 0.6em 0;
  width: 100%;
  // @include media-breakpoint-up(xxl) { 
  //   background-size: 69vw 4em, auto;
  // }
  &.full {
    box-shadow: none;
    background: none;
  }

  .logo {
    width: 5.5em;
    height: auto;
  }

  ul.links-right {
    padding: 0.8em 0;
    margin: 0;
    float: right;
    font-size: 0.8em;
    letter-spacing: 1px;
    text-transform: uppercase;
    text-align: right;
    li {
      list-style: none;
      display: inline-block;
      margin-left: 4em;
      a {
        // color: $nav-links-right;
        font-size: 0.9em;
        // @include media-breakpoint-up(xxl) { 
        //   font-size: 1em;
        // }
        &:hover {
          text-decoration: none;
          // color: $nav-links-right-hover;
        }
      }
    }
  }
}